  margin-left: -18px;
}

.helper-text {
    display: block;
    font-size: 12px;
    color: rgba(0,0,0,0.54);
}

.cancel-btn, .cancel-btn:hover, .cancel-btn:focus  {
  background-color: transparent;
}

.modal-foot {
  background-color: white;
  text-align: right;
  padding-right: 5px;
}

