/* this is for the forms */

.login-btn, .cancel-btn, .main-btn, .update-btn {
  padding: 0px 15px 0px 15px;
}

.input-field>label,
.btn-flat {
  color: white;
}

[type="checkbox"]+span:not(.lever) {
  padding-left: 30px;
}

[type="checkbox"].filled-in:not(:checked)+span:not(.lever):after {
  border: 1px solid white;
}

[type="checkbox"].filled-in:checked+span:not(.lever):after {
  background-color: #344743;
  border: #344743;
}

/* makes the input lines white as well as the text once input */
input[type=text]:not(.browser-default),
input[type=email]:not(.browser-default),
input[type=password]:not(.browser-default),
input[type=number]:not(.browser-default),
input[type=url]:not(.browser-default),
input[type=textarea]:not(.browser-default) {
  border-bottom:1px solid white;
  color: white;
}

/* this changes the line color to match the text color (below) and makes the completed font color white */
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=number]:not(.browser-default):focus:not([readonly]),
input[type=url]:not(.browser-default):focus:not([readonly]),
input[type=textarea]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]),
.select-wrapper input.select-dropdown:focus  {
  border-bottom: 1px solid #BF691E;
  box-shadow: 0 1px 0 0 #BF691E;
}

/* this changes the color of the label's text on the form */
input[type=text]:not(.browser-default):focus:not([readonly])+label,
input[type=password]:not(.browser-default):focus:not([readonly])+label,
input[type=email]:not(.browser-default):focus:not([readonly])+label,
input[type=number]:not(.browser-default):focus:not([readonly])+label,
input[type=url]:not(.browser-default):focus:not([readonly])+label,
input[type=textarea]:not(.browser-default):focus:not([readonly])+label,
textarea.materialize-textarea:focus:not([readonly])+label {
  color: #BF691E;
}

.wrong-credentials {
  color: red;
  float: left;
  background-color: #2a303e;
  border: #2a303e;
  padding: 10px;
  border-radius: 2.5px;
  opacity: 1;
  left: 100px;
}

/* this changes the line color to match the text color (below) and makes the completed font color white */
.no-credentials input[type=text]:not(.browser-default):focus:not([readonly]),
.no-credentials input[type=password]:not(.browser-default):focus:not([readonly]),
.no-credentials input[type=email]:not(.browser-default):focus:not([readonly]),
.no-credentials input[type=number]:not(.browser-default):focus:not([readonly]),
.select-wrapper .no-credentials input.select-dropdown:focus  {
  border-bottom: 1px solid red;
  box-shadow: 0 1px 0 0 red;
}

/* this changes the color of the label's text on the form */
.no-credentials input[type=text]:not(.browser-default):focus:not([readonly])+label,
.no-credentials input[type=password]:not(.browser-default):focus:not([readonly])+label,
.no-credentials input[type=email]:not(.browser-default):focus:not([readonly])+label,
.no-credentials input[type=number]:not(.browser-default):focus:not([readonly])+label {
  color: red;
}

/* adjust label height for all non textarea inputs */
/* .input-field label:not(.label-icon).active {
  transform: translateY(-40%);
} */

/* adjust label height for text area */
.input-field>label:not(.label-icon).active {
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
}

